//core/angular modules
import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';
import { Routes, Router, NavigationStart, NavigationEnd, RouterModule } from '@angular/router';
import { HttpClientModule } from '@angular/common/http';

//vendor modules
import { NgbModule } from '@ng-bootstrap/ng-bootstrap';
import { LaddaModule } from 'angular2-ladda';

//component imports
import { AppComponent } from './app.component';
import { LoginComponent } from './login/login.component';
import { DashboardComponent } from './dashboard/dashboard.component';
import { NavbarComponent } from './navbar/navbar.component';
import { ModalComponent } from './dashboard/modal/modal.component';
import { UserInfoComponent } from './user-info/user-info.component';
import { UserSummaryComponent } from './user-info/user-summary/user-summary.component';

//services
import { CustomerDataService } from './services/customer-data.service';
import { ProductsService } from './services/products.service';
import { FooterComponent } from './footer/footer.component';

const appRoutes: Routes = [
  {path: '', component: DashboardComponent},
  {path: 'user', component: UserInfoComponent},
];

@NgModule({
  declarations: [
    AppComponent,
    LoginComponent,
    DashboardComponent,
    NavbarComponent,
    ModalComponent,
    UserInfoComponent,
    UserSummaryComponent,
    FooterComponent,
  ],
  imports: [
    BrowserModule,
    HttpClientModule,
    LaddaModule.forRoot({
      spinnerColor: "purple",
    }),
    NgbModule.forRoot(),
    RouterModule.forRoot(appRoutes)
  ],
  providers: [
    CustomerDataService,
    ProductsService
  ],
  bootstrap: [AppComponent]
})
export class AppModule { }
