import { Component, OnInit } from '@angular/core';
import { ProductsService } from '../services/products.service';

@Component({
  selector: 'app-user-info',
  templateUrl: './user-info.component.html',
  styleUrls: ['./user-info.component.less']
})
export class UserInfoComponent implements OnInit {
  products;
  marketing;
  incidents;

  constructor(private productsService : ProductsService) { }

  ngOnInit() {
    this.products = this.productsService.getProducts();
    this.marketing = this.productsService.getMarketingInfo();
    this.incidents = this.productsService.getIncidents();
    console.log(this.incidents);
  }


  resolveBadge(status){
    if (status == 'Returned') return 'badge-warning';
    else if (status == 'Purchased') return 'badge-info';
    else if (status == 'Out For Delivery') return 'badge-primary';
    else if (status == 'Delivered') return 'badge-success';
    else if (status == 'incident') return 'badge-warning';
    else if (status == 'failure') return 'badge-danger';
    else if (status == 'bug report') return 'badge-info';
    else if (status == 'fault report') return 'badge-orange';
  }

  resolveIcon(type){
    if (type == 'offer') return 'fa-dollar';
    else if (type == 'coupon') return 'fa-ticket';
    else if (type == 'catalog') return 'fa-book';
    else if (type == 'bug report') return 'fa-bug';
    else if (type == 'failure') return 'fa-exclamation-triangle';
    else if (type == 'incident') return 'fa-info';
    else if (type == 'fault report') return 'fa-exclamation';
  }

  toggleClass($event){
    $event.currentTarget.classList.toggle("text-disabled");
  }
}
